/* CSS declarations go here */

body{
    text-align: center;
}

h1 {
    color:aquamarine;
    border: 2px solid black;
    padding: 5px 10px 0px 0px;
    margin:auto;
    background-color: #333;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

li {
    float: left;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #111;
}